.editor_wrapper {
  width: 100vw;
  height: 100vh;
  background: #fff;
  text-align: center;
}

.textfield,
.preview {
  text-align: left;
  padding: 0 2vw 0 2vw;
}

.textfield {
  height: 30vh;
  width: 92%;
  background: #111;
  color: #eee;
}

code {
  background: #ddd;
}

.preview {
  height: 68vh;
  overflow: scroll;
}
